
/**
 *
 * 获取当前设备IOS?Android
 */
function BrowserInfo () {
  var userAgent = navigator.userAgent
  let isIphone = !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  let isMiniProgram =  window.__wxjs_environment === 'miniprogram'
  var json = {
    isAndroid: userAgent.indexOf('Android') > -1 || userAgent.indexOf('Adr') > -1,
    isIphone: isIphone,
    isWeixin: userAgent.match(/MicroMessenger/i) == "MicroMessenger",
    isMiniProgram: isMiniProgram,
    isIphoneX: screen.height == 812 && screen.width == 375 && isIphone,
    isMiniProgramIphoneX: window.innerHeight >= 642 && isIphone && isMiniProgram
  }
  return json;
}
/**
 *
 * 格式化日期
 * date:日期参数通常为时间戳  fmt：为想要的日期格式如'yyyy-MM-dd'  'yyyy-MM'等
 */
function formatDate (date, fmt) {
  fmt = fmt || 'yyyy-MM-dd hh:mm:ss'
  if (!date || !fmt) {
    return false
  }

  let theDate = getDate(date)
  var o = {
    'M+': theDate.getMonth() + 1, // 月份
    'd+': theDate.getDate(), // 日
    'h+': theDate.getHours(), // 小时
    'm+': theDate.getMinutes(), // 分
    's+': theDate.getSeconds(), // 秒
    'q+': Math.floor((theDate.getMonth() + 3) / 3), // 季度
    'S': theDate.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (theDate.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}


/**
 *
 * 格式化日期
 * 获取格式为：1天前 1分钟前  1个月前
 */

function getDateDiff (dateStr) {
  var publishTime = parseInt(getDate(dateStr).getTime()/1000),
    d_seconds,
    d_minutes,
    d_hours,
    d_days,
    d,
    timeNow = parseInt(new Date().getTime()/1000)

  d = timeNow - publishTime;
  d_days = parseInt(d/86400);
  d_hours = parseInt(d/3600);
  d_minutes = parseInt(d/60);
  d_seconds = parseInt(d);

  if(d_days > 0 && d_days < 30){
    return d_days + '天前';
  }else if(d_days <= 0 && d_hours > 0){
    return d_hours + '小时前';
  }else if(d_hours <= 0 && d_minutes > 0){
    return d_minutes + '分钟前';
  }else if (d_seconds < 60) {
    if (d_seconds <= 0) {
      return '刚刚发表';
    }else {
      return d_seconds + '秒前';
    }
  }else if (d_days >= 30 && d_days < 365){
    return Math.floor(d_days/30) + '个月前';
  }else if (d_days >= 365) {
    return Math.floor(d_days/365) + '年前';
  }
}

function getDate (date) {
  if (!date) {
    return new Date()
  }

  var theDate = ''
  if (typeof date === 'number') {
    theDate = new Date(date)
  } else if (date.length === undefined) {
    theDate = date
  } else {
    if (date.length > 10) {
      var dateArr = date.split(/[T\s]/)

      var beforeTime = dateArr[0].split('-')
      var afterTime = dateArr[1].split(':')

      afterTime[2] = afterTime[2] ? afterTime[2] : '00'
    } else {
      var beforeTime = date.split('-')
    }

    if (afterTime) {
      theDate = new Date(beforeTime[0], beforeTime[1] - 1, beforeTime[2], afterTime[0], afterTime[1], afterTime[2])
    } else {
      theDate = new Date(beforeTime[0], beforeTime[1] - 1, beforeTime[2])
    }
  }

  if (!theDate) {
    theDate = new Date()
  }

  return theDate
}
function formDataDate (str) {
  var dateStr = getDate(str)
  var year = dateStr.getFullYear()
  var monthStr = dateStr.getMonth() + 1
  var dayStr = dateStr.getDate()
  var month = monthStr < 10 ? '0' + monthStr : monthStr
  var day = dayStr < 10 ? '0' + dayStr : dayStr
  return year + '-' + month + '-' + day
}

function formMonthDate (str) {
  var dateStr = getDate(str)
  var year = dateStr.getFullYear()
  var monthStr = dateStr.getMonth() + 1
  var dayStr = dateStr.getDate()
  var month = monthStr < 10 ? '0' + monthStr : monthStr
  return year + '-' + month
}

function formDataTime (str) {
  var dateStr = getDate(str)
  var hours = dateStr.getHours()
  var minutes = dateStr.getMinutes()
  var seconds = dateStr.getSeconds()
  hours = hours < 10 ? '0' + hours : hours
  minutes = minutes < 10 ? '0' + minutes : minutes
  seconds = seconds < 10 ? '0' + seconds : seconds
  return hours + ':' + minutes + ':' + seconds
}

/**
 * 获取上一个月
 *
 * @date 格式为yyyy-mm-dd的日期，如：2014-01-25
 */
function getPreMonth (date) {
  var arr = date.split('-')
  var year = arr[0] // 获取当前日期的年份
  var month = arr[1] // 获取当前日期的月份
  var days = new Date(year, month, 0)
  days = days.getDate() // 获取当前日期中月的天数
  var year2 = year
  var month2 = parseInt(month) - 1
  if (month2 == 0) {
    year2 = parseInt(year2) - 1
    month2 = 12
  }
  var days2 = new Date(year2, month2, 0)
  days2 = days2.getDate()
  if (month2 < 10) {
    month2 = '0' + month2
  }
  var t2 = year2 + '-' + month2
  return t2
}
/**
 * 获取下一个月
 *
 * @date 格式为yyyy-mm的日期，如：2014-01
 */
function getNextMonth (date) {
  var arr = date.split('-')
  var year = arr[0] // 获取当前日期的年份
  var month = arr[1] // 获取当前日期的月份
  var days = new Date(year, month, 0)
  days = days.getDate() // 获取当前日期中的月的天数
  var year2 = year
  var month2 = parseInt(month) + 1
  if (month2 == 13) {
    year2 = parseInt(year2) + 1
    month2 = 1
  }
  var days2 = new Date(year2, month2, 0)
  days2 = days2.getDate()
  if (month2 < 10) {
    month2 = '0' + month2
  }

  var t2 = year2 + '-' + month2
  return t2
}



/**
 * 非同一地址时根据后台url,下载文件
 *
 * url:下载的路径   filename:下载后的文件名
 */
function downloadFile (url, filename) {
  getBlob(url).then(blob => {
    saveAs(blob, filename)
  })
}
function getBlob (url) {
  return new Promise(resolve => {
    const xhr = new XMLHttpRequest()

    xhr.open('GET', url, true)
    xhr.responseType = 'blob'
    xhr.onload = () => {
      if (xhr.status === 200) {
        resolve(xhr.response)
      }
    }

    xhr.send()
  })
}
function saveAs (blob, filename) {
  if (window.navigator.msSaveOrOpenBlob) {
    navigator.msSaveBlob(blob, filename)
  } else {
    const link = document.createElement('a')

    link.href = window.URL.createObjectURL(blob)
    link.download = filename
    link.dispatchEvent(new MouseEvent('click', { bubbles: true, cancelable: true, view: window }))

    window.URL.revokeObjectURL(link.href)
  }
}


/**
 * 获取url里的参数
 *
 * name:需要查找的参数
 */
function getUrlPara (name) {
  let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)","i")
  let r = window.location.search.substr(1).match(reg)

  if (r != null) {
    return (r[2])
  }

  return null;
}
/**
 * 删除URL中的指定参数
 * @param {*} url
 * @param {*} name
 */
function delUrlParams(url, name){
  //根据#号拆分
  let poundArr = url.split('#')
  //？拆分
  let questionArr = []
  if (poundArr) {
    //把#接上
    poundArr.forEach((element,index) => {
      if(index>0){
        element = '#'+ element
      }

      let tempArr = element.split('?')
      if(!tempArr){
        return true
      }
      tempArr.forEach((item, idx) => {
        //保留问号
        if (idx > 0) {
          item = '?'+item
        }
        questionArr.push(item)
      })
    });
  }else{
    questionArr = url.split('?')
    if (questionArr) {
      questionArr.forEach((item, idx)  => {
        if (idx > 0) {
          item = '?'+item
        }
      })
    }
  }

  if(!questionArr){
    return  url
  }

  //&符号的处理
  let andArr = []
  questionArr.forEach((item,index) => {
    let andIdx = item.indexOf('&')
    if (andIdx <= -1) {
      andArr.push(item)
      return true
    }

    let tempAndArr = item.split('&')
    tempAndArr.forEach((ele, idx) => {
      if (idx > 0) {
        ele = '&' + ele
      }
      andArr.push(ele)
    })
  })


  let newUrl = ''
  andArr.forEach(item => {
    let nameIndex = item.indexOf(name+'=')
    //不拼接要删除的参数
    if (nameIndex > -1) {
      //保留第一个问号
      let questionIdx = item.indexOf('?')
      if (questionIdx == 0) {
        newUrl += '?'
      }
      return true
    }
    newUrl += item
  })

  return newUrl.replace(/\?\&/g,"?")
}


export default {
  getDate,
  BrowserInfo,
  formatDate,
  formDataDate,
  formMonthDate,
  formDataTime,
  getDateDiff,
  getPreMonth,
  getNextMonth,
  downloadFile,
  getUrlPara,
  delUrlParams,
}
