import axios from 'axios'
import { Toast } from 'vant';
Toast.setDefaultOptions({ duration: 2000 })

axios.defaults.baseURL = 'https://api.tracker.gg'


axios.interceptors.response.use(
  response => { // 成功请求到数据
    if (response.status === 200  && response.data) {
      return response
    } else {
      Toast.fail('网络异常，请重启APP')
      return Promise.reject(response)
    }
  },
  error => { // 响应错误处理
    if (error &&  error.response && error.response.status) {
      let errorStatus = error.response.status
     if (errorStatus == 500 || errorStatus == 304) {
       Toast.fail('服务器忙碌，请重启APP！')
      } else if (errorStatus == 404) {
       Toast.fail('未查到此用户！')
      }
      return Promise.reject(error)
    }
  }
)


const getMessage = (platform, userName) => {
  return axios.get(`/api/v2/bfv/standard/profile/${platform}/${userName}`).then(res => {
    return res.data
  })
}
