<template>
	<div>
		<div class="contentWrap" v-for="(item,index) in recordInfo"
		     @click="$router.push({path: '/recordInfo', query: { platform: $route.query.platform, userName: $route.query.userName,gameReportId:item.gameReportId }})">
			<span class="timeBox">{{item.timestamp | formatDate(item.timestamp)}}</span>
			<div v-if="item.map" class="imgBox">
				<img :src="item.map.imageUrl" alt="">
			</div>
			<div class="mapName">{{item.modeKey|convertModeName}} - {{item.mapKey|convertMapName}}</div>
			<div class="contentWrapMsk"></div>
		</div>
		<div style="width: 100%;height: 50px"></div>
	</div>
</template>

<script>
  import {getRecord} from '../api'
  import {mapCodeToCN, modeNameToCN} from '../common/js/convertPackage'
  import dayjs from 'dayjs'

  export default {
    name: "record",
    data() {
      return {
        recordInfo: [],
      }
    },
    methods: {
      async getRecordInfo() {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          message: '加载中...'
        })
        const res = await getRecord(this.$route.query.platform, this.$route.query.userName)
        if (!!res) {
          this.recordInfo = JSON.parse(JSON.stringify(res.data.reports))
        }
        this.$toast.clear()
      },
    },
    mounted() {
      this.getRecordInfo()
    },
    filters: {
      formatDate(time) {
        let date = new Date(time * 1000)
        return dayjs(date).format('YYYY-MM-DD dddd HH:mm A')
      },
      convertMapName(value) {
        return mapCodeToCN(value)
      },
      convertModeName(value) {
        return modeNameToCN(value)
      },
    }
  }
</script>

<style lang="less" scoped>
	.contentWrap {
		width: 100%;
		height: 150px;
		position: relative;
		margin-bottom: 1px;
		
		.timeBox {
			width: 62px;
			font-size: 12px;
			position: absolute;
			top: 20px;
			left: 20px;
			color: #fff;
			z-index: 2;
		}
		
		.imgBox {
			width: 100%;
			height: 150px;
			overflow: hidden;
			
			img {
				width: 100%;
				height: 100%;
			}
		}
		
		.mapName {
			width: 100%;
			height: 40px;
			z-index: 2;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20px;
			color: #fff;
		}
	}
	
	.contentWrapMsk {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 150px;
		background: linear-gradient(90deg, rgb(27, 38, 64), transparent);
	}
</style>
