<template>
	<div style="background-color: #253258;color: #fff">
		<div class="heades">
			<img :src="recordInfo.metadata.map.imageUrl" alt="">
			<div class="headesMsk"></div>
			<div class="headesTitle">
				<p>{{recordInfo.metadata.mapKey | convertMapName}}</p>
				<p>{{recordInfo.metadata.modeKey | convertModeName}}</p>
				<p v-if="recordInfo.metadata.timestamp">{{recordInfo.metadata.timestamp | formatDate}}</p>
				<p v-if="recordInfo.metadata.duration">{{recordInfo.metadata.duration | convertTime}}</p>
			</div>
		</div>
		<div v-for="(item,index) in recordInfo.children">
			<div class="headerTop">
				<p>{{item.metadata.name|convertcountryName}}
					<span v-if="item.metadata.isWinner ? true:false" style="color: #cbb765">优胜方</span>
				</p>
			</div>
			<van-row>
				<van-col span="8">玩家</van-col>
				<van-col span="4">分数</van-col>
				<van-col span="3">K/D</van-col>
				<van-col span="3">击杀</van-col>
				<van-col span="3">死亡</van-col>
				<van-col span="3">爆头</van-col>
			</van-row>
			<van-row v-for="(items,indexs) in item.children" :key="index+indexs">
				<van-col span="8" style="margin-top: 10px;font-size: 12px">{{items.metadata.name}}</van-col>
				<van-col span="4" style="margin-top: 10px">{{items.stats[24].displayValue}}</van-col>
				<van-col span="3" style="margin-top: 10px">{{items.stats[2].displayValue}}</van-col>
				<van-col span="3" style="margin-top: 10px">{{items.stats[0].displayValue}}</van-col>
				<van-col span="3" style="margin-top: 10px">{{items.stats[1].displayValue}}</van-col>
				<van-col span="3" style="margin-top: 10px">{{items.stats[5].displayValue}}</van-col>
			</van-row>
		</div>
		<div style="width: 100%;height: 80px"></div>
	</div>
</template>

<script>
  import {getRecordInfo} from '../api'
  import dayjs from 'dayjs'
  import {mapCodeToCN, modeNameToCN, secondsFormat, countryNameConvert} from '../common/js/convertPackage'

  export default {
    name: "recordInfo",
    data() {
      return {
        recordInfo: {
          metadata: {
            map: {}
          },
          children: []
        }
      }
    },
    methods: {
      async getRecords() {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          message: '加载中...'
        })
        const res = await getRecordInfo(this.$route.query.platform, this.$route.query.gameReportId)
        if (!!res) {
          this.recordInfo = res.data
        }
        this.$toast.clear()
      }
    },
    mounted() {
      this.getRecords()
    },
    filters: {
      formatDate(time) {
        let date = new Date(time * 1000)
        return dayjs(date).format('YYYY-MM-DD HH:mm A')
      },
      convertMapName(value) {
        return mapCodeToCN(value)
      },
      convertModeName(value) {
        return modeNameToCN(value)
      },
      convertTime(value) {
        return secondsFormat(value)
      },
      convertcountryName(value) {
        return countryNameConvert(value)
      }
    }
  }
</script>

