<template>
	<div class="home">
		<div class="topImg">
			<img src="../assets/images/fontLogo.png" alt="">
		</div>
		<video autoplay loop muted  style="object-fit:cover;width: 100%;height: 100%;">
			<source src="../assets/bg.mp4" type="video/mp4"/>
		</video>
		<div class="loginBox">
			<van-form @submit="onSubmit">
				<van-cell-group>
					<van-field
						v-model="platform"
						name="游戏平台"
						label="游戏平台"
						placeholder="游戏平台"
						@click="show = true"
						:rules="[{ required: true, message: '请填写游戏平台' }]"
						readonly
					/>
					<van-field
						v-model="userName"
						name="角色名称"
						label="角色名称"
						placeholder="角色名称"
						:rules="[{ required: true, message: '请填写角色名称' }]"
					/>
				</van-cell-group>
				<div class="selectBtn">
					<van-button round block type="info" native-type="submit">查 询</van-button>
				</div>
			</van-form>
		</div>
		
		<van-action-sheet v-model="show" :actions="actions" @select="onSelect"/>
	</div>
</template>

<script>

  export default {
    name: 'information',
    data() {
      return {
        platform: '',
        userName: '',
        show: false,
        actions: [{name: 'PC'}, {name: 'Xbox'}, {name: 'PlayStation'}],
      }
    },
    methods: {
      onSelect(item) {
        if (item.name === 'PlayStation') {
          this.platform = 'psn'
        } else if (item.name === 'Xbox') {
          this.platform = 'xbl'
        } else {
          this.platform = 'origin'
        }
        this.show = false;
      },
      onSubmit() {
        this.$router.push({path: '/About', query: { platform: this.platform, userName: this.userName }})
      },
    },
  }
</script>
