import Vue from 'vue'
import VueRouter from 'vue-router'
import About from '../views/About.vue'
import Home from '../views/Home.vue'
import record from '../views/record.vue'
import recordInfo from '../views/recordInfo.vue'
import gun from '../views/gun.vue'
import car from '../views/car.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      showBar: false
    }
  },
  {
    path: '/About',
    name: 'About',
    component: About,
    meta: {
      showBar: true
    }
  },
  {
    path: '/record',
    name: 'record',
    component: record,
    meta: {
      showBar: true
    }
  },
  {
    path: '/recordInfo',
    name: 'recordInfo',
    component: recordInfo,
    meta: {
      showBar: true
    }
  },
  {
    path: '/gun',
    name: 'gun',
    component: gun,
    meta: {
      showBar: true
    }
  },
  {
    path:'/car',
    name:'car',
    component:car,
    meta:{
      showBar:true
    }
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
