<script>
  import {getMessage, getKdaMessage} from '../api'
  import {userNameConvert} from '../common/js/convertPackage'
  import utils from '../common/js/utils'
  export default {
    name: "About",
    data() {
      return {
        date: '',
        userInfo: {
          platformInfo: {},
          metadata: {
            lastUpdated: {}
          },
          segments: []
        },
        userKda: {
          stats: {
            scorePerMinute: {},
            kdRatio: {},
            kills: {},
            killsPerMinute: {},
            wlPercentage: {},
            wins: {},
            deaths: {},
            assists: {},
            damage: {},
            heals: {},
            revives: {},
            resupplies: {},
          }
        },
        medic: {
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        pilot: {
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        recon: {
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        support: {
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        assault: {
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        tanker:{
          metadata: {},
          stats: {
            kills: {},
            score: {},
            scorePerMinute: {}
          }
        },
        headRank: {
          metadata: {},
          displayValue: ''
        }
      }
    },
    methods: {
      async getUserInfo() {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          message: '加载中...'
        })
        const res = await getMessage(this.$route.query.platform, this.$route.query.userName)
        if (!!res) {
          this.userInfo = res.data
          this.formatDate(res.data.metadata.lastUpdated.value)
          this.assault = res.data.segments[9]
          this.medic = res.data.segments[10]
          this.pilot = res.data.segments[11]
          this.recon = res.data.segments[12]
          this.support = res.data.segments[13]
          this.tanker = res.data.segments[14]
          this.headRank = res.data.segments[0].stats.rank
        }
        const result = await getKdaMessage(this.$route.query.platform, this.$route.query.userName)
        if (!!result) {
          this.userKda = result.data
        }
        this.$toast.clear()
      },
      formatDate(date) {
        let temp = new Date(date)
        this.date = utils.getDateDiff(temp)
      },
    },
    mounted() {
      this.getUserInfo()
    },
    filters:{
      userNameConverts(value){
        return userNameConvert(value)
      }
	  }
  }
</script>

