function timestampToTime(timestamp) {
  var date = new Date(timestamp * 1000)
  var Y = date.getFullYear() + '-'
  var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
  var D = date.getDate() + ' '
  var h = date.getHours() + ':'
  var m = date.getMinutes()
  return Y + M + D + h + m
} // 时间戳转换函数
function mapCodeToCN(mapKey) { // 地图名称转换
  var flag = false
  for (var item in MapKeyToCNName) {
    if (item === mapKey) {
      flag = true
      return MapKeyToCNName[item]
    }
  }
  if (flag === false) return mapKey
}

function modeNameToCN(modelKey) { // 游戏模式名称转换
  var flag = false
  for (var item in ModelKeyToCNName) {
    if (item === modelKey) {
      flag = true
      return ModelKeyToCNName[item]
    }
  }
  if (flag === false) return modelKey
}

function countryNameConvert(name) {
  var flag = false
  for (var item in CountryKeyToCNName) {
    if (item === name) {
      flag = true
      return CountryKeyToCNName[item]
    }
  }
  if (flag === false) return '未知阵营'
}

function timeENGToCN(time) {
  return time.replace('d', '天').replace('h', '小时').replace('m', '分钟').replace('s', '秒').replace(/ /g, '')
}

function weaponNameConvert(name) {
  var flag = false
  for (var item in WeaponKeyToCNName) {
    if (item === name) {
      flag = true
      return WeaponKeyToCNName[item]
    }
  }
  if (flag === false) return name
}

function vehicleNameConvert(name) {
  var flag = false
  for (var item in VehicleKeyToCNName) {
    if (item === name) {
      flag = true
      return VehicleKeyToCNName[item]
    }
  }
  if (flag === false) return name
}

function gadgetNameConvert(name) {
  var flag = false
  for (var item in GadgetKeyToCNName) {
    if (item === name) {
      flag = true
      return GadgetKeyToCNName[item]
    }
  }
  if (flag === false) return name
}
function userNameConvert(name) {
  var flag = false
  for (var item in UserName) {
    if (item === name) {
      flag = true
      return UserName[item]
    }
  }
  if (flag === false) return name
}

function secondsFormat(s) {
  var day = Math.floor(s / (24 * 3600)) // Math.floor() 向下取整
  var hour = Math.floor((s - day * 24 * 3600) / 3600)
  var minute = Math.floor((s - day * 24 * 3600 - hour * 3600) / 60)
  var second = s - day * 24 * 3600 - hour * 3600 - minute * 60
  return (hour > 0 ? hour + '小时' : '') + minute + '分钟' + second + '秒'
}

export {
  timestampToTime,
  mapCodeToCN,
  modeNameToCN,
  countryNameConvert,
  timeENGToCN,
  weaponNameConvert,
  vehicleNameConvert,
  gadgetNameConvert,
  secondsFormat,
  userNameConvert
}
