<template>
	<div style="background: #F7F8FA">
		<div class="top"></div>
		<div>
			<van-row class="rowTitle">
				<van-col span="16">载具</van-col>
				<van-col span="4">击杀</van-col>
				<van-col span="4">摧毁</van-col>
			</van-row>
			<van-row class="classBox" v-for="(item,index) in carInfo" :key="index">
				<van-col span="16" class="nameContent">
					<div>
						<img :src="item.metadata.imageUrl" alt="" v-if="item.metadata.imageUrl">
					</div>
					<p>{{item.metadata.name | carNameConvert}}</p>
				</van-col>
				<van-col span="4" class="scoreContent">
					<p>{{item.stats[0].displayValue}}</p>
				</van-col>
				<van-col span="4" class="scorePerMinute">{{item.stats[3].displayValue}}</van-col>
			</van-row>
		</div>
		<div style="width: 100%;height: 60px"></div>
	</div>
</template>

<script>
  import {getCarInfo} from '../api'
  import {vehicleNameConvert} from '../common/js/convertPackage'
  export default {
    name: "gun",
    data(){
      return{
        carInfo:[]
      }
    },
    methods:{
      async getCarsInfo() {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          message: '加载中...'
        })
        const res = await getCarInfo(this.$route.query.platform, this.$route.query.userName)
        if(!!res){
          this.carInfo = res.data.children
        }
        this.$toast.clear()
      }
    },
    mounted() {
      this.getCarsInfo()
    },
    filters:{
      carNameConvert(value){
        return vehicleNameConvert(value)
      }
    }
  }
</script>

